import { useEffect, useState } from "react";
import Title from "../../Helpers/Title";
import ReCAPTCHA from "react-google-recaptcha";
import { StyledAccountsContainer, StyledContactButton, StyledContactDiv, StyledGITH1, StyledGITSection, StyledInfoContainer } from "../StyledComponents";
import { Slide, Snackbar, TextField } from "@mui/material";
import Alert from '@mui/material/Alert';
import emailjs from '@emailjs/browser';
import Loader from "../../Loader/Loader";

export default function GetInTouch() {
    const recaptchaTheme = localStorage.getItem('lightMode') === 'l' ? 'light' : 'dark';
    const [formValues, setFormValues] = useState({
        name: '',
        surname: '',
        email: '',
        message: ''
    });
    const [isSending, setIsSending] = useState(false);
    const [recaptchaToken, setRecaptchaToken] = useState(null);
    const [alertShow, setAlertShow] = useState(false);
    const [alertType, setAlertType] = useState();
    const [alertMessage, setAlertMessage] = useState();
    const [isAlertOpen, setIsAlertOpen] = useState(false);
    const [isMobile, setIsMobile] = useState(window.innerWidth < 768);

    useEffect(() => {
        const handleResize = () => {
            setIsMobile(window.innerWidth < 768);
        };

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    const SlideTransition = (props) => {
        return <Slide {...props} direction="left" />;
    }
    const showAlert = (type, message) => {
        setIsAlertOpen(true);
        setAlertType(type);
        setAlertMessage(message);
        setAlertShow(true);
    };
    const handleAlertClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setIsAlertOpen(false);
        setAlertShow(false);
    };

    const handleChange = (event, maxLength) => {
        const { id, value } = event.target;
        if (value.length <= maxLength) {
            setFormValues((prevValues) => ({
                ...prevValues,
                [id]: value
            }));
        }
    };

    const handleFormSubmit = async () => {
        setIsSending(true);
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

        if (!formValues.name || !formValues.surname || !formValues.email || !formValues.message) {
            setIsSending(false);
            showAlert("error", "Fill in all the fields below to continue!");
            return;
        }
        if (!emailRegex.test(formValues.email)) {
            setIsSending(false);
            showAlert("error", "Please enter a valid email address");
            return;
        }
        const res = await handleMailSend(formValues.name, formValues.surname, formValues.email, formValues.message);
        if (res.status === 200) {
            setFormValues({
                name: '',
                surname: '',
                email: '',
                message: ''
            })

            showAlert("success", "Everything was successfully sent");
        } else {
            showAlert("error", "An error occurred while sending the message. Please try again later.");
        }
        setIsSending(false);
    };

    const handleMailSend = async (name, surname, email, message) => {
        try {
            const res = await emailjs.send(process.env.REACT_APP_EMAIL_SERVICE, process.env.REACT_APP_EMAIL_TEMPLATE,
                {
                    name: name,
                    surname: surname,
                    email: email,
                    message: message,
                }
                , process.env.REACT_APP_EMAIL_SERVICE_KEY);
            return res;
        } catch (error) {
            throw error;
        }
    };
    return (
        <>
            <Title title="Get In Touch" />
            <StyledGITH1>Get In Touch</StyledGITH1>
            <StyledGITSection>
                <div>
                    <h2>Don't be shy!</h2>
                    <p>I'm always available to discuss new projects, creative ideas or opportunities to be a part of your vision!</p>
                    <small>{recaptchaToken ? "Great, here's my contacts!" : "To un-hide my contacts, please complete the captcha"}</small>
                    <ReCAPTCHA
                        sitekey={process.env.REACT_APP_RECAP_KEY}
                        onChange={(token) => setRecaptchaToken(token)}
                        theme={recaptchaTheme}
                        style={{ display: recaptchaToken ? 'none' : 'block' }}
                    />
                    {recaptchaToken &&
                        <StyledAccountsContainer>
                            <StyledInfoContainer>
                                {isMobile ? <h3>+39 366 4054006</h3> : <h2>+39 366 4054006</h2>}
                                <svg width="48" viewBox="0 0 24 24" data-name="Line Color" xmlns="http://www.w3.org/2000/svg"><path d="M21 15v3.93a2 2 0 0 1-2.29 2A18 18 0 0 1 3.14 5.29 2 2 0 0 1 5.13 3H9a1 1 0 0 1 1 .89 10.7 10.7 0 0 0 1 3.78 1 1 0 0 1-.42 1.26l-.86.49a1 1 0 0 0-.33 1.46 14.1 14.1 0 0 0 3.69 3.69 1 1 0 0 0 1.46-.33l.49-.86a1 1 0 0 1 1.3-.38 10.7 10.7 0 0 0 3.78 1 1 1 0 0 1 .89 1" style={{ fill: 'none', stroke: '#9b27ff', strokeLinecap: 'round', strokeLinejoin: 'round', strokeWidth: '2' }} /></svg>
                            </StyledInfoContainer>
                            <StyledInfoContainer>
                                {isMobile ? <h3>michael.lavigna@hotmail.it</h3> : <h2>michael.lavigna@hotmail.it</h2>}
                                <svg width="48" viewBox="0 0 24 24"><path fill="#9b27ff" d="M19 7H5a2 2 0 0 0-2 2v9a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2V9a2 2 0 0 0-2-2m-9.684 7.316 1.602 1.4c.305.266.691.398 1.082.398s.777-.133 1.082-.398l1.602-1.4-.037.037 3.646 3.646H5.707l3.646-3.646zM5 17.293V10.54l3.602 3.151zm10.398-3.602L19 10.54v6.75zM19 9v.21l-6.576 5.754a.68.68 0 0 1-.848 0L5 9.21V9z" /></svg>
                            </StyledInfoContainer>
                        </StyledAccountsContainer>}
                </div>
                <StyledContactDiv>
                    <h2 style={{ textAlign: 'center' }}>Fill in the form to send me an email!</h2>
                    <TextField classes={{ root: 'mail-field' }} id="name" label="Name" variant="filled" helperText="Please enter your name" value={formValues.name} onChange={(event) => handleChange(event, 100)} />
                    <TextField classes={{ root: 'mail-field' }} id="surname" label="Surname" variant="filled" helperText="Please enter your surname" value={formValues.surname} onChange={(event) => handleChange(event, 100)} />
                    <TextField classes={{ root: 'mail-field' }} id="email" label="Email" variant="filled" helperText="Please enter your email" value={formValues.email} onChange={(event) => handleChange(event, 150)} />
                    <TextField classes={{ root: 'mail-field' }} id="message" label="Enter your message..." variant="filled" helperText="Please enter your message" multiline maxRows={6} value={formValues.message} onChange={(event) => handleChange(event, 255)} />
                    <StyledContactButton style={{ margin: '1vh auto' }} onClick={handleFormSubmit}>Send</StyledContactButton>
                </StyledContactDiv>
                {alertShow && (
                    <Snackbar style={{ zIndex: '9999', marginTop: '10vh' }} open={isAlertOpen} autoHideDuration={3000} onClose={handleAlertClose} TransitionComponent={SlideTransition} anchorOrigin={{ vertical: 'top', horizontal: 'right' }} key={{ vertical: 'top', horizontal: 'right' }}>
                        <Alert
                            style={{ background: 'rgb(255 255 255 / 90%)' }}
                            onClose={handleAlertClose}
                            severity={alertType}
                            variant="outlined"
                            sx={{ width: '100%' }}
                        >
                            {alertMessage}
                        </Alert>
                    </Snackbar>)}
            </StyledGITSection>
            {isSending && <Loader />}
        </>
    );
}